.story {
    font-family: 'Poppins', 'Roboto', sans-serif;
    font-size: 18px;
    display: flex;
    flex-direction: column;    
    text-align: center;
    align-items: center;
    flex: 1 0 15%; 
    margin: 5%;
    
    
}

p {
    text-align: center;
}

.thumbnail {
    border-radius: 50%;
    width: 150px;
    margin-top: 2em;    
}

.thumbnail:hover {
    border: 1px solid #b7b7a4;
}

.type {
    border-bottom: 2px solid #b7b7a4;
    padding-bottom: 0.25em;
}

.link {    
    border-bottom: 1px solid #b7b7a4;
    text-decoration: none;
    color: inherit;
    transition: all 0.2s linear;
    padding-bottom: .15em;   
     
}

.link:hover {
    border-bottom: 2px solid #6b705c;
}