.list {
    list-style-type: none;
    font-weight: 700;
}

.home {
    font-family: 'Poppins', 'Roboto', sans-serif;
    font-size: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left; 
    min-height: 100vh;
}
.home-text {
    text-align: left;
    margin-left: 10px;
}


.portfolioList {
    list-style-type: none;
    text-decoration: none;
    
    padding: 0;
    text-align: left;
}


.link {    
    border-bottom: 1px solid #b7b7a4;
    text-decoration: none;
    color: inherit;
    transition: all 0.2s linear;
    padding-bottom: .15em;    
}

.link:hover {
    border-bottom: 2px solid #6b705c;
}

li {
    display: inline-block;
    padding-right: 0.5em;
    font-family: 'Poppins', sans-serif;
    margin-left:10px
    
}



a:hover {
    border-color: #f43e3e
}

