.story-container {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin: 0 15%;       

}

.list-container {
    display: flex;
    justify-content: center;
}

h1 {
    font-family: 'Poppins', 'Roboto', sans-serif; 
    text-align: center;
    margin-top: 2%;
    
}

@media screen and (max-width: 800px) {
    h1 {
        text-align: center;
        margin-left: 0px;
    }
    .story-container {
        margin: 0;   
    }
}